
<template>
  <div class="xmbw-dialog">
    <el-dialog title="" width="800px" :visible.sync="visible" :append-to-body="true" :close-on-click-modal="false" @close="$emit('update:show', false)" :show="show">
      <el-form ref="form" :model="form" label-width="140px" size="mini" style="padding:20px;" label-position="top" 
      :inline="true">
          <el-form-item label="" required>
              <el-date-picker style="width:750px" v-model="form.cnwcrq" type="date" value-format="yyyy-MM-dd" placeholder=""></el-date-picker>
          </el-form-item>
          <div>
            <div class="cpList-option" v-for="(item,index) in cpDataList" flex>
              <div flex colcenter style="padding:0 10px;"> 
                <el-form-item label="" required >
                    <el-select v-model="item.cp"  placeholder="" >
                    <el-option
                      v-for="(cp,index) in cpList"
                      :key="index"
                      :label="cp.cpmc"
                      :value="cp.cpdm+'&'+cp.cpmc">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="(/)" required>
                <el-input  placeholder="" v-model="item.ssgzl"></el-input>
                </el-form-item>
                <el-form-item label="(/)" required>
                <el-input   placeholder="" v-model="item.ekgzl"></el-input>
                </el-form-item>
                <el-form-item label="()">
                <el-input   placeholder="" v-model="item.kbgzl"></el-input>
                </el-form-item>
              </div>
              <div class="cpList-delete"  v-if="index !== 0">
                  <span  @click="handleDelete(index)" title=""  class="el-icon-error"></span>
              </div>
            </div>
            <div>
              <a href="jvaScript:;;" @click="handleAddcp"><span class="el-icon-circle-plus"></span></a>
            </div>
          </div>
          <el-form-item label="" required>
           <el-input style="width:750px" type="textarea" :rows="3" placeholder="" v-model="form.sm"></el-input>
          </el-form-item>
        </el-form>  
      <div text-right class="pd-10">
        <el-button type="primary" size="mini" @click="handleCommitMilestone"></el-button>
        <el-button size="mini" type="" @click="handleCloseDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { queryMilestoneOperationrecords } from "@/api/milestone.js";
import { getMenu, getSession } from "@/utils/util.js";

import axios from "axios";
import Qs from "qs";
export default {
  data() {
    return {
      visible: this.show,
      form: {
        cnwcrq: "",
        sm: "",
        cps:""
      },
      cpDataList:[{
        cp:'',
        cpbh:'',
        cpmc:'',
        ssgzl:0,
        ekgzl:0,
        kbgzl:0
      }],

      cpList:[],
      isValid:true
    };
  },
  methods: {
    handleCloseDialog() {
      this.visible = false;
    },
    handleCommitMilestone() {
      if(!this.validate()) return;
      this.$emit("handleCommitXmbw", this.form,this.isValid);
    },
    // 
    handleAddcp(){
      this.cpDataList.push({
        cp:'',
        cpbh:'',
        cpmc:'',
        ssgzl:0,
        ekgzl:0,
        kbgzl:0
      })
    },
    // 
    handleDelete(index){
      this.cpDataList.splice(index,1);
    },
    validate(){
      this.form.cps = '';
      this.cpDataList.forEach(ele=>{
        ele.cpmc = ele.cp.split('&')[1];
        ele.cpbh = ele.cp.split('&')[0];
        if(!ele.cp){
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
         this.isValid = false;
         return;
        }
        if(!/^[0-9]+\d*$/.test(ele.ssgzl)){
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
         this.isValid = false;
         return;
        }
        if(!/^[0-9]+\d*$/.test(ele.ekgzl)){
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          this.isValid = false;
          return;
        }
        if(ele.kbgzl == ''){
           ele.kbgzl = 0;
          }else if (!/^\d+(\.\d+)?$/.test(ele.kbgzl)) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          this.isValid = false;
          return;
        }
        this.isValid = true;
        this.form.cps += ele.cpbh + String.fromCharCode(1) + ele.cpmc + String.fromCharCode(1) + ele.ssgzl + String.fromCharCode(1)
        + ele.ekgzl + String.fromCharCode(1) + ele.kbgzl + String.fromCharCode(2)
      })

      if (!this.form.cnwcrq) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return false;
      }
      if (!this.form.sm) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return false;
      }
      return true;
    },

    // 
    listHtnrApp(){
      this.$get(this.API.listHtnrApp,{
       xmbh:this.xmbh
      }).then(res=>{
        if(res.state == 'success'){
          this.cpList = res.data
        }else{
          this.$alert(res.msg, "", {confirmButtonText: "",type: "warning"});
        }
      })
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    xmbh:{
      type:String,
      default:""
    }
  },
  watch: {
    show(n, o) {
      this.visible = this.show;
      if (!n) {
        this.form.cnwcrq = this.form.sm = "";
        this.form.ssgzl =  this.form.ekgzl = this.form.kbgzl =  0;
        this.cpDataList = [{
                  cp:'',
                  cpbh:'',
                  cpmc:'',
                  ssgzl:0,
                  ekgzl:0,
                  kbgzl:0
                }]
      } else {
        this.listHtnrApp();
      }
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.cpList-option{
  border-bottom:1px dashed #999;
}
.cpList-delete{
  line-height: 80px;
  span{
    color: #f00;
    &:hover{
      cursor: pointer;
    }
  }
}
</style>



